@import url(https://fonts.googleapis.com/css2?family=Lato:wght@400;700&display=swap);
* {
  padding: 0;
  margin: 0;
  box-sizing: border-box;
  font-family: 'Lato', sans-serif;

}

.App {
  background-color: #f4faef;
}

.app-container {
  min-height: 600px;
}

.section-container{
  max-width: 800px;
  margin: 0 auto;
}
.navbar, .dropdown-menu{
  background-color: #a6da9a !important;
  color:#fff !important;
}

.dropdown-menu{
  border: none !important;
}
.logo-div {
  max-width: 300px;
  margin: 20px auto;
}

.logo {
  width: 100%;
}

.home-header {
  text-align: center;
}

.intro-section {
  display: flex;
  margin-top: 40px;
}

.intro-text {
  width: 70%;
  margin-right: 30px;
}

.photo-div {
  display: flex;
  justify-content: center;
  width: 25%;
}

.intro-text {
  line-height: 25px;
}

.photo {
  width: 100%;
  border-radius: 5px;
}

.specialties-intro{
  margin-top: 40px;
}

.subtitle{
  text-align: center;
  color: #428b30;
  margin-bottom: 20px;
}

.specialty-text:not(:last-child) {
  margin-bottom: 15px; 
}

.specialty-span{
  font-weight: 700;
}

.verse{
  font-style: italic;
}

.how-it-works{
  margin-top: 50px;
}

.works-text:not(:last-child){
  margin-bottom: 15px;
}
.about-header {
  text-align: center;
}

.education-experience-title {
  text-align: center;
  color: #428b30;
  margin: 40px 0 20px 0;
}

.education-experience-div {
  display: flex;
  justify-content: space-between;
}

.education-experience-subtitle {
  font-style: italic;
}

.education-experience-subtitle:not(:first-of-type) {
  margin-top: 5px;
}






.rates-title{
  text-align: center;
  margin: 30px 0;
}

.insurance:not(:last-child){
margin-bottom: 15px;
}
.contact-title{
  text-align: center;
  margin: 30px 0;
}

.contact-div {
  display: flex;
  justify-content: space-between;
}

.form-div,
.contact-details {
  width: 48%;
}

.form-input {
  width: 100%;
  margin-bottom: 20px;
  border: 1.5px solid #428b30;
  background-color: #f4faef;
  padding: 15px 5px;
}

.contact-methods{
  margin-top: 20px;
}

.contact-icon {
  background-color: #428b30;
  padding: 10px;
  border-radius: 100%;
  color: #f4faef;
  font-size: 20px;
}

.email,
.phone-number{
  margin-bottom: 20px;
}

.phone-number, .email-link{
color: #428b30;
font-size: 24px;
}
.footer {
  background-color: #a6da9a;
  text-align: center;
  color: #428b30;
  padding: 30px 0;
  margin-top: 50px;
}

.contact-info {
  display: flex;
  justify-content: center;
  margin: 20px 0 15px 0;
  font-weight: 700;
}

.separator {
  margin: 0 10px;
}


